.section {
  background: url("../../../assets/img/sponsors/Sponsors_bg.webp") center top
    fixed no-repeat;
  background-size: cover;
  padding: 60px 0 60px 0;
  position: relative;
}
.section::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.92);
  z-index: 9;
}
.container {
  position: relative;
  z-index: 10;
}

.content {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-flow: row wrap;
  padding: 0 50px;
}

.logoHover:hover {
  transition: 500ms;
  background: rgba(69, 69, 69, 0);
  cursor: pointer;
  background-size: cover;
  transform: scale(1.1);
}
