.navbar .navbar-nav .nav-link {
  line-height: 1.6;
  margin: 5px 3px;
  padding: .5rem 1rem;
  opacity: .8;
  text-transform: uppercase;
  font-weight: 600;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #f6f3ee;
}
.navbar-toggler {
  border: none;
}
.navbar{
  border: 0;
  font-size: 14px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  padding: 0;
  background: #ff6535;
  box-shadow: 0 6px 10px -4px rgba(0, 0, 0, 0.15);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.navbar-transparent{
  background: transparent !important;
  padding-top: 25px;
  box-shadow: none;
  font-size: 15px;
}
@media (min-width: 770px) {
  .nav-link:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}
@media (max-width: 770px) {
  .navbar-brand {
    margin-left: 20px;
  }
  .navbar-toggler {
    margin-right: 20px;
  }
}


.navbar-transparent .navbar-nav .nav-link{
  color: #f6f3ee;
}

a {
  color: #FFF;
}
a:hover{
  color: white;
  background-color: transparent;
  text-decoration: none;
}

