/*   Custom ScrollBar      */
/*************************
*/

::-webkit-scrollbar {
  width: 12px;
}


::-webkit-scrollbar-track {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 4px rgba(0,0,0,0.3);
}


::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(192, 192,192, 0.9);

}

::-webkit-scrollbar-thumb:hover{
  background: rgba(169,169,169,0.8);
}
::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(192,192,192,1);
}


.section-header h3 {
  font-size: 32px;
  color: #111;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  position: relative;
  padding-bottom: 15px;
}

.section-header h3::before {
  content: '';
  position: absolute;
  display: block;
  width: 120px;
  height: 1px;
  background: #ddd;
  bottom: 1px;
  left: calc(50% - 60px);
}

.section-header h3::after {
  content: '';
  position: absolute;
  display: block;
  width: 40px;
  height: 3px;
  background: #ff6535;
  bottom: 0;
  left: calc(50% - 20px);
}

.section-header p {
  text-align: center;
  padding-bottom: 30px;
  color: #29588c;
  font-size: 1.4em;
}

.content-center h1 , .content-center h2 {
    text-shadow: 5px 5px 5px #403c32;
}

.about-title {
    color: #ff6535;
}

.us-title {
    color: #29588c !important;
}

#about {
    background: url(/static/media/about-bg.e8c79ae6.webp) center top no-repeat fixed;
    background-size: cover;
    padding: 60px 0 40px 0;
    position: relative;
}

#about::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.92);
    z-index: 9;
}

#about .container {
    position: relative;
    z-index: 10;
}

#about .about-col {
    background: #fff;
    border-radius: 0 0 4px 4px;
    box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.08);
    margin-bottom: 20px;
}

#about .about-col .img {
    position: relative;
}

#about .about-col .img img {
    border-radius: 4px 4px 0 0;
}

#about .about-col .icon {
    width: 64px;
    height: 64px;
    padding-top: 8px;
    text-align: center;
    position: absolute;
    background-color: #ff6535;
    border-radius: 50%;
    text-align: center;
    border: 4px solid #fff;
    left: calc( 50% - 32px);
    bottom: -30px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

#about .section-header p {
    font-family: 'Roboto' , SansSerif;
    text-align: center;
}

#about .about-col i {
    font-size: 36px;
    line-height: 1;
    color: #fff;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
#about .about-col {
    height: 100%;
}

#about .about-col:hover .icon {
    background-color: #fff;
}

#about .about-col:hover i {
    color: #ff6535;
}

#about .about-col h2 {
    color: #000;
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    padding: 0;
    margin: 40px 0 12px 0;
}
#about .about-col p {
    font-family: 'Roboto' , SansSerif;

}

#about .about-col h2 a {
    color: #29588c;
}

#about .about-col h2 a:hover {
    color: #81a0aa;
}

#about .about-col p {
    font-size: 1.2em;
    line-height: 24px;
    color: #333;
    margin-bottom: 0;
    padding: 0 20px 20px 20px;
    text-align: justify;
}

#team {
  padding: 60px 0;
}

.team--bg {
  background-color: #fcfcfc;
}

.team__title-style {
  margin-bottom: 30px;
}

.team__title-style .first-line {
  display: inline-block;
  border-top: 3px solid #1680f8;
  border-bottom: 3px solid #1680f8;
  padding: 0px 50px;
  border-radius: 5px;
  position: relative;
}

.team__title-style .first-line:after {
  content: "";
  width: 7px;
  height: 7px;
  display: inline-block;
  border-radius: 50%;
  position: absolute;
  right: -17px;
  top: -4px;
  background-color: #1680f8;
}

.team__title-style .second-line {
  display: inline-block;
  border-top: 3px solid #1680f8;
  border-bottom: 3px solid #1680f8;
  padding: 0px 18px;
  border-radius: 5px;
  position: relative;
  margin-left: 38px;
}

.team__title-style .second-line:before {
  content: "";
  width: 7px;
  height: 7px;
  display: inline-block;
  border-radius: 50%;
  position: absolute;
  left: -15px;
  top: -4px;
  background-color: #1680f8;
}

.team__single {
  border-radius: 10px;
  border: 0;
  box-shadow: 0px 2px 20px 0px rgba(0, 0, 0, 0.1);
  padding: 30px 25px;
}

@media (max-width: 991px) {
  .team__single {
    margin-bottom: 50px;
  }
}

.team__single .bio img {
  object-fit: cover;
  width: 75px;
  height: 75px;
  border-radius: 50%;
}

.team__single .bio h2 {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 20px;
  color: #ff6535;
}

.team__single .bio p {
  font-size: 16px;
  color: #29588c;
}

.team__single .caption p {
  font-size: 16px;
  color: black;
  line-height: 1.8;
  margin-bottom: 25px;
  font-family: Roboto, SansSerif;
  text-align: justify;
}

.team__single .caption .social-icons li {
  display: inline-block;
  margin-right: 15px;
  vertical-align: middle;
}

.team__single .caption .social-icons li a {
  display: block;
}

.team__single .caption .social-icons li [class^="flaticon-"]:before,
.team__single .caption .social-icons li [class*=" flaticon-"]:before {
  margin: 0;
  width: 22px;
  height: 22px;
  display: block;
  border-radius: 50%;
  font-size: 12px;
  padding-top: 2px;
  text-align: center;
  background-color: #29588c;
  color: white;
}

.member-card {
  margin-top: 20px;
  margin-bottom: 15px;
}

.team-section-description {
  color: #29588c;
  font-size: 20px;
}

.role-name {
  color: #29588c !important;
}

