.hero .feat-item {
    padding: 0;
    margin-top: 5%;
}
.hero .feat-item .text-center {
    padding: 50px 30px;
    background-color: #fff;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.04);
}
.hero .feat-item.active .text-center {
    margin-top: -25px;
    padding-top: 75px;
    box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.05);
    position: relative;
    z-index: 3;
}
.hero .feat-item:hover .icon {
    color: #fff;
}
.hero .feat-item:hover .icon:after {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
}
.hero .feat-item .icon {
    width: 90px;
    height: 90px;
    line-height: 105px;
    color: #777;
    font-size: 60px;
    border-radius: 50%;
    margin-bottom: 15px;
    position: relative;
    z-index: 3;
}
.hero .feat-item .icon:after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #5c5c5c;
    background: -webkit-gradient(linear, left top, right bottom, from(#dcdcdc), to(#5c5c5c));
    background: -webkit-linear-gradient(top left, #dcdcdc, #5c5c5c);
    background: linear-gradient(to bottom right, #dcdcdc, #5c5c5c);
    border-radius: 50%;
    z-index: -1;
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
    -webkit-transition: all .4s;
    transition: all .4s;
}
.hero .feat-item h5 {
    font-weight: 500;
    font-size: 18px;
    margin-bottom: 10px;
    color: black;
    margin-top: 30px;
}
.hero .feat-item h6 {
    color: #1A237E;
    font-size: 12px;
    font-weight: 400;
    font-style: italic;
    margin-bottom: 20px;
}




