.card-block {
    font-size: 1em;
    position: relative;
    margin: 0;
    padding: 1em;
    border: none;
    box-shadow: none;


}
.card {
    margin: 20px auto;
    max-width: 90%;
    font-size: 1em;
    overflow: hidden;
    border: none;
    transition: .3s all ease-in-out;
    box-shadow: 5px 15px 45px -9px rgba(0,0,0,.2);
    border-radius: unset;
    margin-bottom: 70px ;



}
.card:hover{
    box-shadow: none;
    top : -1px;
}
