.header{
    background: url("../../assets/img/Member_of_the_week/header2-bg.webp") center top no-repeat fixed;
    height: 100vh;
    background-size: cover ;
    padding: 120px 0 40px 0;
    position: relative;
    width: 100%;
}
@media (max-width: 1300px) {
    .header{height: fit-content}

}
.header::before{
    content:'';
    position: absolute;
    top:0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(219, 213, 213,0.75);
    z-index: 5;
}

.container{
    margin: 0 100px;
    position: relative;
    z-index: 100;
    /*width: 100vw;*/
}
@media (max-width: 500px) {
    .container{
        margin: 0 40px;
    }
    .currentMember img{
       height: 50vh;
    }
}

.currentMember{
    padding: 40px 0;

}
.currentMember img{
    /*width: 80%;*/
    max-height: 50vh;
    min-width: 30vh;
    min-height: 400px;
    border-radius: 5%;
    margin-top: 50px;
    /*padding-left: 60px ;*/
}
@media (max-width: 1100px) {
    .currentMember img {
        min-height: 300px;
    }
}
.currentMember h1 {
    margin: 0 0 10px 0;
    font-size: 55px;
    font-weight: 700;
    line-height: 56px;
    color: #fff;
    font-family: Raleway,'sans-serif';
}

.currentMember p {
    margin-bottom: 35px;
    font-size: 22px;
    font-family: 'Roboto', sans-serif;
    color: black;
    text-align: justify;
}
.currentMember p span {
    color: #fff;
    padding-bottom: 4px;
    letter-spacing: 1px;
    border-bottom: 3px solid #29588c;
    font-family: 'Poppins', sans-serif;
    font-size: 28px;

}

.section{
    background: url("../../assets/img/bg.webp") center top no-repeat fixed;
    background-size: cover;
    padding: 60px 0 60px 0;
    position: relative;
}
.section::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.88);
    z-index: 9;
}

.prevContent{
    padding: 40px 0;

}
.img-fluid {
    width: 100%;
    height: 100%;
}
